import React from 'react'
import './react-vis-style.css';
import {XYPlot, LineSeries, XAxis, YAxis, Hint} from 'react-vis';

const Waves = ({ lon, lat, waves }) => {
  var viz_waves =[];
  for (let k in waves) {
      viz_waves.push({x: k, y:waves[k]});
  }

  return (
    <div>
      <center><p>Your input latitude is {lat !== '' ? lat : 'unknown'} and longitude is {lon !== '' ? lon : 'unknown'}</p></center>

      <center>
      <XYPlot xType="ordinal" yDomain={[3, 7]} height={500} width={800}>
        <XAxis title="Year"/>
        <YAxis title="Wave Heights"/>
        <LineSeries data={viz_waves} />
      </XYPlot>
      </center>

      <center>
      {Object.keys(waves).map((key, index) => (
        <div class="card" style={{width: 20 + 'rem'}}>
          <div class="card-body">
            <h5 class="card-title">Year: {key}</h5>
            <p class="card-text">Wave Height: {waves[key]}</p>
          </div>
        </div>
      ))}
      </center>

    </div>
  )
};



export default Waves
