import React, { Component } from 'react';
import Waves from './components/waves';

class App extends Component {
    constructor() {
      super();
      this.state = {
        lon: '',
        lat: '',
        waves: {}
      }
      this.handlechangeLat = this.handlechangeLat.bind(this);
      this.handlechangeLon = this.handlechangeLon.bind(this);
      this.handlebuttonclick = this.handlebuttonclick.bind(this);
    }

    handlechangeLat(e) {
      this.setState({lat: e.target.value});
    }

    handlechangeLon(e) {
      this.setState({lon: e.target.value});
    }

    handlebuttonclick(e) {
        fetch('https://europe-west1-freesolarcalc.cloudfunctions.net/wave_max/?lat='+this.state.lat+'&lon='+this.state.lon)
            .then(
                res => res.json()
            ).then((data) => {
                this.setState({
                    waves: data
                })
            })
            .catch((error) => {
                if (error.toString().includes("Failed to fetch")){
                    alert("Failed to fetch data.")
                }
            })
    }

    // componentDidMount() {
    //     fetch('https://europe-west1-freesolarcalc.cloudfunctions.net/wave_max/?lat=4.6&lon=1.3')
    //         .then(
    //             res => res.json()
    //         ).then((data) => {
    //             this.setState({
    //                 waves: data
    //             })
    //         })
    //         .catch(console.log)
    // }

    render() {
        return (
            <div>
                <center><h1>Wave Heights</h1></center>
                <center>Latitude: <input type='number' step="0.1" placeholder="Enter latitude" onChange={this.handlechangeLat}/></center>
                <center>Longitude: <input type='number' step="0.1" placeholder="Enter longitude" onChange={this.handlechangeLon}/></center>
                <center><button style={{margin: 20 + 'px'}} type="button" class="btn btn-primary" onClick={this.handlebuttonclick}>
                    Get Information
                </button></center>
                <Waves lat={this.state.lat} lon={this.state.lon} waves={this.state.waves}/>
            </div>
        )
    }
}

export default App;
